/*

This routine checks the credit card number. The following checks are made:

1. A number has been provided
2. The number is a right length for the card
3. The number has an appropriate prefix for the card
4. The number has a valid modulus 10 number check digit if required

If the validation fails an error is reported.

The structure of credit card formats was gleaned from a variety of sources on the web, although the 
best is probably on Wikepedia ("Credit card number"):

  http://en.wikipedia.org/wiki/Credit_card_number

Parameters:
            cardnumber           number on the card
            cardname             name of card as defined in the card list below

Author:     John Gardner
Date:       1st November 2003
Updated:    26th Feb. 2005      Additional cards added by request
Updated:    27th Nov. 2006      Additional cards added from Wikipedia
Updated:    18th Jan. 2008      Additional cards added from Wikipedia
Updated:    26th Nov. 2008      Maestro cards extended
Updated:    19th Jun. 2009      Laser cards extended from Wikipedia
Updated:    11th Sep. 2010      Typos removed from Diners and Solo definitions (thanks to Noe Leon)
Updated:    10th April 2012     New matches for Maestro, Diners Enroute and Switch
Updated:    17th October 2012   Diners Club prefix 38 not encoded
Updated:    7 July 2016         New MasterCard "2" series.
            3rd April 2017      Converted to typescript by Kevin Voyce
*/

/*
   If a credit card number is invalid, an error reason is loaded into the global ccErrorNo variable. 
   This can be be used to index into the global error  string array to report the reason to the user 
   if required:
   
   e.g. if (!checkCreditCard (number, name) alert (ccErrors(ccErrorNo);
*/
import { Injectable } from '@angular/core';

//Local
import { Constants } from '../../../app.settings';

@Injectable()

export class CreditcardValidationService {

    public cardPassthrough: string = '';
    public ccErrorNo: number = 0;
    public ccErrors: string[] = [
        "Unknown card type",
        "No card number provided",
        "Credit card number is in invalid format",
        "Credit card number is invalid",
        "Credit card number has an inappropriate number of digits",
        "Credit card number is invalid"
    ];
    

  checkCreditCard(cardnumber: string, cardname: string): boolean {
    console.log("What cardNUmber r we validating: ", cardnumber)
      this.ccErrorNo = null;
        // Array to hold the permitted card characteristics
        var cards = new Array();

        // Define the cards we support. You may add additional card types as follows.

        //  Name:         As in the selection box of the form - must be same as user's
        //  Length:       List of possible valid lengths of the card number for the card
        //  prefixes:     List of possible prefixes for the card
        //  checkdigit:   Boolean to say whether there is a check digit

        cards[0] = {
            name: "Visa",
            length: "13,16",
            prefixes: "4",
            checkdigit: true
        };
        cards[1] = {
            name: "MasterCard",
            length: "16",
            prefixes: "22,23,24,25,26,27,51,52,53,54,55",
            checkdigit: true
        };
        cards[2] = {
            name: "DinersClub",
            length: "14,16",
          prefixes: "360,361,362,363,364,365,367,368,369,380,381,382,383,384,385,386,387,388,389",
            checkdigit: true
        };
        //cards[3] = {
        //    name: "CarteBlanche",
        //    length: "14",
        //    prefixes: "300,301,302,303,304,305",
        //    checkdigit: true
        //};
        cards[3] = {
            name: "CarteBlanche",
            length: "14",
            prefixes: "300,301,302,303,304,305,306,307,308,309,",
            checkdigit: true
        };
        cards[4] = {
            name: "AmEx",
            length: "15",
            prefixes: "34,37",
            checkdigit: true
        };
        cards[5] = {
            name: "Discover",
            length: "16",
            prefixes: "6011,622,64,65",
            checkdigit: true
        };
        cards[6] = {
            name: "JCB",
            length: "16,19",
          prefixes: "3528,353,354,356,357,358",
            checkdigit: true
        };
        cards[7] = {
            name: "enRoute",
            length: "15",
            prefixes: "2014,2149",
            checkdigit: true
        };
        cards[8] = {
            name: "Solo",
            length: "16,18,19",
            prefixes: "6334,6767",
            checkdigit: true
        };
        cards[9] = {
            name: "Switch",
            length: "16,18,19",
            prefixes: "4903,4905,4911,4936,564182,633110,6333,6759",
            checkdigit: true
        };
        cards[10] = {
            name: "Maestro",
            length: "12,13,14,15,16,18,19",
            prefixes: "5018,5020,5038,6304,6759,6761,6762,6763",
            checkdigit: true
        };
        cards[11] = {
            name: "VisaElectron",
            length: "16",
            prefixes: "4026,417500,4508,4844,4913,4917",
            checkdigit: true
        };
        cards[12] = {
            name: "LaserCard",
            length: "16,17,18,19",
            prefixes: "6304,6706,6771,6709",
            checkdigit: true
        };

        // Establish card type
        var cardType = -1;
        for (var i = 0; i < cards.length; i++) {
            // See if it is this card (ignoring the case of the string)
            if (cardname.toLowerCase() == cards[i].name.toLowerCase()) {
                cardType = i;
                break;
            }
        }

        // If card type not found, report an error
        if (cardType === -1) {
            this.ccErrorNo = 0;
            return false;
        }

        // Ensure that the user has provided a credit card number
        if (cardnumber.length === 0) {
            this.ccErrorNo = 1;
            return false;
        }

        // Now remove any spaces from the credit card number
        cardnumber = cardnumber.replace(/\s/g, "");

        this.cardPassthrough = cardnumber;

        // Check that the number is numeric
        var cardNo:string = cardnumber
        var cardexp:RegExp  = /^[0-9]{13,19}$/;
        if (!cardexp.exec(cardNo)) {
            this.ccErrorNo = 2;
            return false;
        }

        // Now check the modulus 10 check digit - if required
        if (cards[cardType].checkdigit) {
            var checksum:number = 0;                                  // running checksum total
            var mychar:string = "";                                   // next char to process
            var j:number = 1;                                         // takes value of 1 or 2

            // Process each digit one by one starting at the right
            var calc:number;
            for (i = cardNo.length - 1; i >= 0; i--) {

                // Extract the next digit and multiply by 1 or 2 on alternative digits.
                calc = Number(cardNo.charAt(i)) * j;

                // If the result is in two digits add 1 to the checksum total
                if (calc > 9) {
                    checksum = checksum + 1;
                    calc = calc - 10;
                }

                // Add the units element to the checksum total
                checksum = checksum + calc;

                // Switch the value of j
                if (j === 1) { j = 2 } else { j = 1 };
            }

            // All done - if checksum is divisible by 10, it is a valid modulus 10.
            // If not, report an error.
          if (checksum % 10 !== 0) {
            console.log("Did we get an invalid checksum: ", checksum)
                this.ccErrorNo = 3;
                return false;
            }
        }

        // Check it's not a spam number
        if (cardNo === '5490997771092064') {
            this.ccErrorNo = 5;
            return false;
        }

        // The following are the card-specific checks we undertake.
        var LengthValid:boolean = false;
        var PrefixValid:boolean = false;
        var undefined;

        // We use these for holding the valid lengths and prefixes of a card type
        var prefix:string[];
        var lengths:number[];

        // Load an array with the valid prefixes for this card
      prefix = cards[cardType].prefixes.split(",");
      //console.log("Valid prefix: ", prefix)
        
        // Now see if any of them match what we have in the card number
        for (var i = 0; i < prefix.length; i++) {
            var exp:RegExp = new RegExp("^" + prefix[i]);
            if (exp.test(cardNo)) PrefixValid = true;
        }


        // If it isn't a valid prefix there's no point at looking at the length
        if (!PrefixValid) {
            this.ccErrorNo = 3;
            return false;
        }

        // See if the length is valid for this card
        lengths = cards[cardType].length.split(",");
        for (var j = 0; j < lengths.length; j++) {
            if (cardNo.length == lengths[j]) {
                LengthValid = true;
                break;
            }
        }

        // See if all is OK by seeing if the length was valid. We only check the length if all else was 
        // hunky dory.
        if (!LengthValid) {
            this.ccErrorNo = 4;
            return false;
        };

        // The credit card is in the required format.
        return true;
    }
}
